.artcard-container {
  position: relative;
  width: 200px;
  height: 280px;
  background-color: #fff;
  display: inline-block;
  margin: 10px;
  border-radius: 4px;
  overflow: hidden;
  -webkit-animation: fadein 1s;
  animation: fadein 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.artcard-container:hover {
  -webkit-box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.2);
}

.artcard-container .image {
  opacity: 1;
  display: block;
  width: 100%;
  height: 210px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  text-align: center;
  background: -webkit-linear-gradient(0, rgba(46, 202, 127, 0.4) 0%, rgba(46, 202, 127, 0.01) 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(0), color-stop(0%, rgba(0, 0, 0, 0.08)), to(#fff));
  background: -webkit-linear-gradient(0, rgba(0, 0, 0, 0.08) 0%, #fff 100%);
  background: -o-linear-gradient(0, rgba(0, 0, 0, 0.08) 0%, #fff 100%);
  background: linear-gradient(0, rgba(0, 0, 0, 0.08) 0%, #fff 100%);
}

.artcard-container .image img {
  margin-top: 25px;
  height: 160px;
  -webkit-filter: drop-shadow(8px 6px 5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(8px 6px 5px rgba(0, 0, 0, 0.5));
}

.artcard-container .image .year {
  font-size: 12px;
  color: #171717;
  position: absolute;
  top: 195px;
  right: 5px;
  font-style: italic;
}

.artcard-container:hover .image {
  opacity: 0.3;
}

.artcard-container:hover .middle {
  width: 100%;
  height: 210px;
  opacity: 1;
  cursor: pointer;
}

.artcard-container .middle {
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 210px;
  text-align: center;
  line-height: 210px;
  background: -webkit-linear-gradient(-45deg, rgba(46, 202, 127, 0.4) 0%, rgba(46, 202, 127, 0.01) 100%);
  background: -webkit-linear-gradient(315deg, rgba(46, 202, 127, 0.4) 0%, rgba(46, 202, 127, 0.01) 100%);
  background: -o-linear-gradient(315deg, rgba(46, 202, 127, 0.4) 0%, rgba(46, 202, 127, 0.01) 100%);
  background: linear-gradient(135deg, rgba(46, 202, 127, 0.4) 0%, rgba(46, 202, 127, 0.01) 100%);
}

.artcard-container .desc {
  color: #171717;
  text-align: center;
  padding: 10px 5px 2px 5px;
  display: block;
  font-size: 14px;
}

.artcard-container .desc .subdesc {
  color: #8c8c8c;
  text-transform: lowercase;
  font-size: 13px;
  margin-top: 2px;
}

@keyframes fadein {
  from {
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}


